//   id     
//   watched,    -   
export function getWatchedFilmsByUser(userId) {
  const result = [];
  const dbRef = ref(db, `user/${userId}/watched/`);
  onValue(
    dbRef,
    snapshot => {
      snapshot.forEach(childSnapshot => {
        result.push(childSnapshot.val());
      });
    },
    {
      onlyOnce: true,
    }
  );
  return result;
}

