//       watched
//   id   id   ( API)
//    -  true,   - false
// !!!!!!!!       await -  

export async function isWatched(userId, filmId) {
  let result = false;
  const dbRef = ref(db, `user/${userId}/watched/`);
  const snapshot = await get(dbRef);
  snapshot.forEach(childSnapshot => {
    if (childSnapshot.val().id === filmId) {
      result = true;
    }
  });
  return result;
}

